/* Colors for main site skin */
$color__skin-main:    rgb(100,0,48);
$color__skin-acc1:    rgb(255,250,250);
$color__skin-acc2:    rgb(255,235,50);
$color__skin-dark:    rgb(0,0,0);
$alpha__skin-tint:    .82;

/* Colors for secondary site skin */
$color__skin2-main:     darken(rgb(0,90,128), 10%);
$color__skin2-acc1:     rgb(255,250,250);
$color__skin2-acc2:     rgb(255,235,50);
$color__skin2-dark:     rgb(0,0,0);
$alpha__skin2-tint:     .82;

/* Misc Colors */
$color__background-body: #fff;
$color__background-screen: #f1f1f1;
$color__background-hr: #ccc;
$color__background-button: #e6e6e6;
$color__background-pre: #eee;
$color__background-ins: #fff9c0;

$color__text-screen: #21759b;
$color__text-input: #666;
$color__text-input-focus: #111;
$color__link: royalblue;
$color__link-visited: purple;
$color__link-hover: midnightblue;
$color__text-main: #404040;

$color__border-button: #ccc #ccc #bbb;
$color__border-button-hover: #ccc #bbb #aaa;
$color__border-button-focus: #aaa #bbb #bbb;
$color__border-input: #ccc;
$color__border-abbr: #666;
